<template>
  <div class="center statusInfo" v-loading.body="loading">
    <div class="center_breadcrumb" style="font-size:14px; height: 84px; background-color: transparent;line-height:84px;">
      <!-- <Crumb /> -->
      <span style="color: #696969;cursor: pointer;" @click="back">学前诊断</span> > <span style="color: #03B187;">学习状态分析详情</span>
    </div>
    <div class="center_content">
      <!-- 导出 -->
      <!-- <div class="center_content_title">
        <div class="derive-btn">
          <el-button type="success" icon="el-icon-tickets" round size="mini" @click.native.prevent="exportList">导出名单</el-button>
        </div>
      </div> -->
      <div class="center_content_search">
        <div style="margin-bottom: 50px; color: #fff; font-size:24px;text-align:center; position: relative">
          <div style="width:120px;height:28px;border:1px solid rgba(176,249,224,1);border-radius:14px;font-size:14px;line-height:28px;float: right;cursor: pointer; position: absolute; top: 0; left: 0" @click="back">
            <SvgIcon icon-class="iconfanhui"></SvgIcon>  返回列表
          </div>
          学习状态分析详情
        </div>
        <div class="userInfo">
          <div>学生姓名：{{info.name}}</div>
          <div>学号：{{info.code}}</div>
          <div>所属班级：{{info.className}}</div>
          <div>课程名称：{{course.name}}</div>
        </div>
      </div>
        <!-- 搜索 -->
        
    </div>
    <div id="charts"></div>
    <div class="tableInfo">
      <div class="table">
        <el-table
          :data="tableData"
          stripe=""
          style="width: 100%">
          <el-table-column
            prop="grade"
            label="毕业要求指标点">
          </el-table-column>
          <el-table-column
            prop="val"
            label="直接达成度">
          </el-table-column>
          <el-table-column
            prop="score"
            label="间接达成度">
          </el-table-column>
          <el-table-column
            prop="targetVal"
            label="期望达成度">
          </el-table-column>
          <el-table-column
            prop="classVal"
            label="班级平均达成度">
          </el-table-column>
        </el-table>
      </div>
      <div class="text">
        <h3>学习状态反馈</h3>
        <div v-html="html"></div>
      </div>
    </div>
  </div>
</template>

<script>
import echarts from 'echarts'
export default {
  name: "StudentLearningStatus",
  components: {},
  data() {
    return {
      info: {},
      course: {},
      tableInfo: {},
      tableData: [],
      html: '',
      loading: true
    }
  },
  created() {
    this.info = JSON.parse(this.$route.query.info)
    this.course = JSON.parse(this.$route.query.course)
    this.tableInfo = JSON.parse(this.$route.query.tableInfo)
  },
  mounted() {
    this.loading = true
    const xAxis = []
    const val = []
    const tmpVal = []
    const targetVal = []
    const classVal = []
    const listQuery = JSON.parse(this.$route.query.listQuery)
    this.$get('/teacher/learningState/learningForPoint', {grade: listQuery.grade, classId: this.info.classId, courseId: listQuery.courseId, studentUserId: this.info.sys_user_id}).then(res => {
      this.loading = false
      const tmp = []
      res.forEach(item => {
        tmp.push(item.score)
      })
      this.tableInfo.forEach((item, i) => {
        xAxis.push(item.grade)
        val.push(this.info[`val${i+1}`])
        targetVal.push(this.info[`targetVal${i+1}`])
        classVal.push(this.info[`classVal${i+1}`])
      })
      this.initCharts(xAxis, val, tmp, targetVal, classVal)
      this.initTable(tmp)
      this.initText()
    })
  },
  methods: {
    back() {
      this.$router.push({
        name: 'studentLearningStatus',
        query: {
          listQuery: this.$route.query.listQuery
        }
      })
    },
    initCharts(xAxis, val, tmpVal, targetVal, classVal) {
      const charts = echarts.init(document.getElementById('charts'))
      let option = {
        title : {
          text: '指标点达成情况对比',
          top: '5%',
          left: '30px'
        },
        tooltip : {
          trigger: 'axis',
          axisPointer : {
            type : 'shadow'
          }
        },
        grid: {
          left: '5%',
          right: '5%'
        },
        legend: {
          data:['直接达成度','间接达成度','期望达成度','班级平均达成度'],
          top: '5%',
          right: '5%',
          itemWidth: 12,
          itemHeight: 12
        },
        toolbox: {
          show : true,
          feature : {
            saveAsImage : {show: true}
          },
          top: '3%',
          right: '2%'
        },
        calculable : true,
        xAxis : [
          {
            type : 'category',
            data : xAxis,
            axisLine:{
              show:false
            },
            axisTick:{
              show:false
            }
          }
        ],
        yAxis : [
          {
            type : 'value',
            axisLine:{
              show:false
            },
            axisTick:{
              show:false
            }
          }
        ],
        series: [
          {
            name:'直接达成度',
            type:'bar',
            barGap: '0',
            data:val,
            barMaxWidth: 60,
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#03B187'
                  },
                  {
                    offset: 1,
                    color: '#8EE9CA'
                  }
                ]),
              }
            }
          },
          {
            name:'间接达成度',
            type:'bar',
            barGap: '0',
            data:tmpVal,
            barMaxWidth: 60,
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#FFA351'
                  },
                  {
                    offset: 1,
                    color: '#FFE098'
                  }
                ]),
              }
            }
          },
          {
            name:'期望达成度',
            type:'bar',
            barGap: '0',
            data:targetVal,
            barMaxWidth: 60,
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#5AB3FF'
                  },
                  {
                    offset: 1,
                    color: '#B3DCFF'
                  }
                ]),
              }
            }
          },
          {
            name:'班级平均达成度',
            type:'bar',
            barGap: '0',
            data:classVal,
            barMaxWidth: 60,
            itemStyle: {
              normal: {
                color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  {
                    offset: 0,
                    color: '#D5CFFF'
                  },
                  {
                    offset: 1,
                    color: '#D5CFFF'
                  }
                ]),
              }
            }
          }
        ]
      }
      charts.setOption(option)
      window.addEventListener('resize', function(){
        charts.resize()
      })
    },
    initTable(tmp) {
      this.tableInfo.forEach((item, i) => {
        this.tableData.push({
          grade: item.grade,
          val: this.info[`val${i+1}`],
          score: tmp[i],
          targetVal: this.info[`targetVal${i+1}`],
          classVal: this.info[`classVal${i+1}`],
        })
      })
    },
    initText() {
      let row = this.info
      let obj = this.course
      let html = `<div>至本学期，${row.name}同学在"${obj.name}"支撑的毕业要求指标点达成情况：</div>`
      this.tableInfo.forEach((item, i) => {
        if(row['targetVal' + (i + 1)] == 0) {
          html += `<p><span style="font-weight: 700">毕业要求${item.grade}</span>：未开始此能力的学习`
        } else {
          html += `<p><span style="font-weight: 700">毕业要求${item.grade}</span>：达成值为"${row['val' + (i + 1)]}"，
          ${row['val' + (i + 1)] >= row['targetVal' + (i + 1)] ? `超出预期达成值"${row['targetVal' + (i + 1)]}"` : `<span style="color: #FF5858">未达到预期达成值"${row['targetVal' + (i + 1)]}"</span>` }，
          ${row['val' + (i + 1)] >= row['classVal' + (i + 1)] ? `超出班级平均值"${row['classVal' + (i + 1)]}"` : `<span style="color: #FFA351">未达到班级平均值"${row['classVal' + (i + 1)]}</span>"`}
          ${row['val' + (i + 1)] >= row['targetVal' + (i + 1)] && row['val' + (i + 1)] >= row['classVal' + (i + 1)] ? '，继续保持即可。' : '，在后续学习中还需加强。'}</p>`
        }
        
      })
      this.html = html
    }
  }
};
</script>

<style lang='scss'>
@import "../../styles/center.scss";
.statusInfo {
  margin-bottom: 100px;
  .center_content {
    overflow: hidden;
    .center_content_title {
      position: relative;
      .derive-btn {
        position: absolute;
        right: 0;
        top: 10px;
        .el-button {
          // padding: 7px 23px 3px 23px;
          float: left;
          i,
          span {
            font-size: 12px;
            color: #fff;
          }
          i {
            margin-top: 0;
          }
          span {
            font-weight: normal;
            margin-left: 0;
          }
        }
      }
    }
    .center_content_search {
      padding: 30px;
      box-sizing: border-box;
      background: url('../../assets/img/bg-chaxun.png') !important;
      background-size: 100% 100%;
      .el-input__inner {
        background-color: #16be95;
        color: #fff
      }
      .el-select .el-input.is-focus .el-input__inner,
      .el-input__inner:focus {
        border-color: #fff;
      }
      .userInfo {
        display: flex;
        margin-bottom: 20px;
        div {
          flex: 1;
          text-align: center;
          font-size:16px;
          color: #fff;
        }
      }
    }
    .el-row {
      .el-select {
        width: 90%;
      }
    }
  }
  #charts {
    height:260px;
    background:rgba(255,255,255,1);
    border-radius:10px;
    border: 1px solid transparent;
    box-sizing: border-box;
    &:hover {
      border-color: #FFA351;
    }
  }
  .tableInfo {
    margin-top: 20px;
    overflow: hidden;
    .table {
      float: left;
      width: 49%;
      margin-right: 2%;
      padding-bottom: 20px;
      border-radius: 10px;
      background-color: #fff;
      min-height: 300px;
      box-sizing: border-box;
      border: 1px solid transparent;
      &:hover {
        border-color: #FFA351;
      }
    }
    .text {
      float: left;
      width: 49%;
      background-color: #fff;
      padding: 30px;
      border-radius: 10px;
      box-sizing: border-box;
      min-height: 300px;
      border: 1px solid transparent;
      &:hover {
        border-color: #FFA351;
      }
      h3 {
        font-size: 18px;
        color: #323232;
        margin-bottom: 20px;
      }
      >div>div {
        letter-spacing: 0.2em;
        font-size: 14px;
        line-height: 2;
      }
      p {
        letter-spacing: 0.2em;
        font-size: 14px;
        line-height: 2;
        margin-bottom: 20px;
      }
    }
  }
}
</style>



